.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #333;
    padding: 1rem 2rem;
    color: #fff;
  }
  
  .navbar h1 {
    font-size: 2rem;
  }
  
  .navbar ul {
    list-style: none;
    display: flex;
    gap: 1.5rem;
  }
  
  .navbar ul li {
    font-size: 1.1rem;
  }
  
  .navbar ul li a {
    color: #fff;
    text-decoration: none;
    transition: color 0.3s ease;
  }
  
  .navbar ul li a:hover {
    color: #f0a500;
  }
  