.about-page {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
  }
  
  .about-content {
    flex: 1;
    padding: 2rem;
    max-width: 800px;
    margin: 0 auto;
    background-color: #f8f8f8;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .about-content h1 {
    text-align: center;
    margin-bottom: 1.5rem;
  }
  
  .about-content p {
    font-size: 1.2rem;
    line-height: 1.6;
    color: #555;
  }
  