.product-3d-viewer {
    width: 100%;
    height: 0;
    padding-bottom: 75%; /* Adjust based on the desired aspect ratio */
    position: relative;
    overflow: hidden;
  }
  
  model-viewer {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    transition: transform 0.3s ease-in-out;
  }
  
  .product-3d-viewer:hover model-viewer {
    transform: scale(1.1); /* Scale up on hover */
  }
  
  .product-card {
    transition: transform 0.3s ease;
  }
  
  .product-card:hover {
    transform: scale(1.05); /* Scale up on hover for the card */
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3); /* Add a subtle shadow */
  }
  