.home-page {
    font-family: 'Arial', sans-serif;
    color: #333;
  }
  
  .home-page-header {
    text-align: center;
    background-color: #f4f4f4;
    padding: 2rem;
  }
  
  .home-page-header h1 {
    font-size: 2.5rem;
    margin-bottom: 1rem;
  }
  
  .home-page-header p {
    font-size: 1.2rem;
    color: #777;
  }
  
  .product-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
  }
  
  .product-card {
    width: 30%;
    margin: 1rem;
    padding: 1rem;
    border: 1px solid #ddd;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
  }
  
  .product-card img {
    width: 100%;
    height: auto;
    border-radius: 10px;
  }
  
  .product-card h2 {
    font-size: 1.5rem;
    margin: 1rem 0;
  }
  
  .product-card p {
    font-size: 1rem;
    color: #666;
  }
  
  .product-card:hover {
    transform: scale(1.05);
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3);
  }
  